











import { Component, Prop, Vue } from 'vue-property-decorator';

@Component
  export default class XInput extends Vue {

  @Prop(String)
  public readonly text!: string;

  @Prop({ type: String, default: '#6e6e6e' })
  public readonly color!: string;

  get colorStyles() {
    return {
      color: this.color,
    }
  }

};
